  <div v-on:scroll="onScroll">
    <div class="main"
         style="background-image: url('https://source.unsplash.com/1080x1024/?space?nasa?planet')">

      <div class="flex flex-col h-full justify-center">
        <div class="flex flex-col p-10">
          <span class="text-3xl  uppercase font-extrabold text-gray-100 w-full text-center">Depuis que les oiseaux sont devenus blanc, La beauté est devenu l'art</span>
          <span class="text-sm text-center text-teal-200">HIRO HAMADA</span>

        </div>

        <div class="flex flex-col w-full">
          <input type="text"
                 class="outline-none focus:outline-none  w-4/6  py-5 px-8 mx-auto rounded-full opacity-75 placeholder-gray-600"
                 placeholder="Recherchez des belles images">

          <div v-if="!visible" class="tag-suggestion flex flex-wrap justify-center text-teal-100">
            <span class="w-full text-center font-bold mb-4 text-white">Suggestion: </span>
            <span class="tag">Forêt</span>
