            <span class="tag">Fleures</span>
            <span class="tag">Nature</span>
            <span class="tag">Beauté</span>

          </div>
        </div>

      </div>


    </div>

    <div style="max-width:82rem" class="mx-auto flex my-8 px-2">

      <div class="flex flex-col w-1/3">
        <ImageTile v-for="(item, index) in row1.images" :key="index" :image-data="item"
                   :short="item.short" :normal="item.normal" :long="item.long"></ImageTile>
      </div>
      <div class="flex flex-col w-1/3 mx-4 lg:mx-8">
        <ImageTile v-for="(item, index) in row2.images" :key="index" :image-data="item"
                   :short="item.short" :normal="item.normal" :long="item.long"></ImageTile>
      </div>

      <div class="flex flex-col w-1/3">
        <ImageTile v-for="(item, index) in row3.images" :key="index" :image-data="item"
                   :short="item.short" :normal="item.normal" :long="item.long"></ImageTile>
      </div>

    </div>


  </div>
</template>
<style scoped>


  .main {

    @apply h-screen/3 w-full bg-fixed bg-green-200 bg-cover relative
  }

  .main > * {
    z-index: 2;
    @apply relative w-full;

